import React from 'react'
import classes from './Help.module.css'
import Converter1 from '../../../../docs/Dock1.pdf'
import Converter2 from '../../../../docs/Dock2.pdf'
import Converter3 from '../../../../docs/Dock3.pdf'
import Converter4 from '../../../../docs/Dock4.pdf'


const Help = () =>{

    return (
        <div className={classes.Help}>
            <h1>Помощь</h1>
            <p>Уважаемые владельцы магазинов! В данном разделе вы можете посмотреть документы с подробным описанием основных моментов возникающих при работе с сервисом azato.ru.</p>
            <a className={classes.infoLinks} target="_blank" rel="noopener noreferrer" href={Converter2}>Загрузка файла на сайт</a>
            <a className={classes.infoLinks} target="_blank" rel="noopener noreferrer" href={Converter1}>Как выгрузить прайс-лист из 1С в Excel</a>
            <a className={classes.infoLinks} target="_blank" rel="noopener noreferrer" href={Converter3}>Если на вашем компьютере не установлена программа MS Excel</a>
            <a className={classes.infoLinks} target="_blank" rel="noopener noreferrer" href={Converter4}>Проверка и редактирование прайс-листа</a>
        </div>
    )
}

export default Help